import { Component, Input } from '@angular/core';
import { Note } from '../note';
import { NoteService } from '../../../services/note.service';

@Component({
  selector: 'app-note-details',
  templateUrl: './note-details.component.html',
  styleUrls: ['./note-details.component.css']
})
export class NoteDetailsComponent {
  @Input()
  note: Note;

  @Input()
  createHandler: Function;
  @Input()
  updateHandler: Function;
  @Input()
  deleteHandler: Function;

  constructor (private noteService: NoteService) {}

  createNote(note: Note) {
    this.noteService.createNote(note).subscribe((newNote: Note) => {
      this.createHandler(newNote);
    });
  }

  updateNote(note: Note): void {
    this.noteService.updateNote(note).subscribe((updatedNote: Note) => {
      this.updateHandler(updatedNote);
    });
  }

  deleteNote(noteId: String): void {
    this.noteService.deleteNote(noteId).subscribe((deletedNoteId: String) => {
      this.deleteHandler(deletedNoteId);
    });
  }
}



