import { Injectable } from '@angular/core';
import { AuthService } from './auth.service';
import { Note } from '../components/dashboard/note';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class NoteService {

  constructor(private authService: AuthService, private http: Http) { }

  getNotes() {
    let headers = new Headers();
    let requestUrl = 'users/notes';
    let authToken = this.authService.lendToken();
    headers.append('Authorization', authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.get(requestUrl, {headers: headers})
      .map(res => res.json());
  }

  createNote(note) {
    let headers = new Headers();
    let requestUrl = 'users/notes';
    let authToken = this.authService.lendToken();
    headers.append('Authorization', authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.post(requestUrl, note, {headers: headers})
      .map(res => res.json());
  }

  deleteNote(noteId) {
    let headers = new Headers();
    let requestUrl = 'users/notes' + '/' + noteId;
    let authToken = this.authService.lendToken();
    headers.append('Authorization', authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.delete(requestUrl, {headers: headers})
      .map(res => res.json());
  }

  updateNote(note) {
    let headers = new Headers();
    let requestUrl = 'users/notes';
    let authToken = this.authService.lendToken();
    headers.append('Authorization', authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.put(requestUrl, note, {headers: headers})
      .map(res => res.json());
  }
}



